.card {
  position: relative;
  padding: 1rem;
  color: white;
  border-radius: 35px;
  display: inline-block;
  margin: 1rem 0.5rem;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  transition: all 0.2s ease-in-out;
}
.card::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 35px;
  transition: all 0.2s ease-in-out;
  pointer-events: none;
}
.card:hover {
  transform: translate(-10px, -10px);
}
.card:hover::after {
  transform: translate(10px, 10px);
}
.card__red {
  background-color: #93312d;
}
.card__blue {
  background-color: #5234b1;
}
.card__green {
  background-color: #326932;
}
.card__purple {
  background-color: #a12dc9;
}
.card__pink {
